<template>
  <div>
    <div class="banner" @click="handleGallaryClick">
      <img
        class="banner-img"
        :src="bannerImg"
        alt="banner"
      >
      <div class="banner-info">
        <div class="banner-title">{{sightName}}</div>
        <div class="banner-number">
          <span class="iconfont banner-icon">&#xe64b;</span>
          15
        </div>
      </div>
    </div>
    <fade-animation>
      <common-gallary
        :imgs="gallaryImgs"
        v-show="showGallary"
        @closeGallary="closeGallary"
      >
      </common-gallary>
    </fade-animation>

  </div>
</template>

<script>
import CommonGallary from 'common/gallary/Gallary'
import FadeAnimation from 'common/fade/FadeAnimation'
export default {
  name: 'DetailBanner',
  props: {
    sightName: String,
    bannerImg: String,
    gallaryImgs: Array
  },
  data () {
    return {
      showGallary: false
    }
  },
  components: {
    CommonGallary,
    FadeAnimation
  },
  methods: {
    handleGallaryClick () {
      this.showGallary = true
    },
    closeGallary () {
      this.showGallary = false
    }
  }
}
</script>

<style lang="stylus" scoped>
  .banner
    position :relative
    overflow :hidden
    height :0
    padding-bottom :55%
    .banner-img
      width :100%
    .banner-info
      display :flex
      position :absolute
      left :0
      right :0
      bottom :0
      color :#fff
      line-height :.54rem
      background-image :linear-gradient(top , rgba(0,0,0,0), rgba(0,0,0,0.8))
      .banner-title
        flex :1
        float :left
        margin-left :.2rem
        overflow :hidden
        white-space :nowrap
        text-overflow :ellipsis
      .banner-number
        float right
        height :.42rem
        line-height :.42rem
        margin-top :.06rem
        margin-right :.2rem
        background :rgba(0,0,0,0.9)
        border-radius :.2rem
        padding :0 .2rem
        font-size :.2rem
        .banner-icon
          font-size :.15rem
</style>



