<template>
  <div class="comment-container">
    <div class="comment-title"></div>
    <div class="comment-wraper border-top" v-for="item of commentList" :key="item.id">
      <div class="content-attr">
        <span class="iconfont content-grade">&#xe60b;&#xe60b;&#xe60b;&#xe60b;&#xe60b;</span>
        <span class="content-time">{{item.username}} &nbsp;&nbsp;{{item.time}}</span>
      </div>
      <div class="content-txt">{{item.content}}</div>
      <div class="content-imgs" @click="handleGallaryClick">
        <div class="imgs-wraper" v-for="(img,index) of item.commentImgs" :key="index">
          <img class="content-img" :src="img" alt="">
        </div>
      </div>
      <fade-animation>
        <common-gallary
          :imgs="item.commentImgs"
          v-show="showGallary"
          @closeGallary="closeGallary"
        >
        </common-gallary>
      </fade-animation>
    </div>
    <div class="all-comment-btn"> <span class="iconfont">&#xe66b;</span></div>
  </div>
</template>

<script>
import CommonGallary from 'common/gallary/Gallary'
import FadeAnimation from 'common/fade/FadeAnimation'
export default {
  name: 'DetailComment',
  props: {
    commentList: Array
  },
  data () {
    return {
      showGallary: false
    }
  },
  components: {
    CommonGallary,
    FadeAnimation
  },
  methods: {
    handleGallaryClick () {
      this.showGallary = true
    },
    closeGallary () {
      this.showGallary = false
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .border-top
    &:before
      border-color:#ccc
  .comment-container
    padding :0 .2rem
    background :#fff
    margin-top :.2rem
    .comment-title
      position :relative
      color: #333
      font-size: .3rem
      line-height: .88rem
      height :.88rem
      text-indent: .2rem
    .comment-title:after
      content :''
      position :absolute
      top :.31rem
      left :0
      width: .06rem
      height: .26rem
      background: $bgColor
    .comment-wraper
      padding :.3rem 0
      .content-attr
        height :.54rem
        line-height :.54rem
        .content-grade
          float :left
          font-size :.24rem
          color :#ffb436
        .content-time
          float :right
          font-size :.24rem
      .content-txt
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow ellipsis
        line-height: .42rem;
        font-size: .26rem;
        color: #616161;
        padding :.1rem 0
      .content-imgs
        width :100%
        overflow :hidden
        .imgs-wraper
          float :left
          width :33%
          padding :.05rem
          box-sizing :border-box
          .content-img
            width :100%
    .all-comment-btn
      line-height :.88rem
      text-align :center
      color: #333
</style>



