<template>
  <div class="info-container">
    <div class="info-attr">
      <div class="info-grade border-right">
        <div class="grade-wraper">
          <span class="fraction">4.9</span><span class="grade"> &nbsp;</span>
        </div>
        <div class="comment">1717</div>
        <span class="iconfont more-icon">&#xe66b;</span>
      </div>
      <div class="info-introduction">
        <div class="attractions">
          
        </div>
        <div class="time"></div>
        <span class="iconfont more-icon">&#xe66b;</span>
      </div>
    </div>
    <div class="address border-top">
      <span class="iconfont site-icon">&#xe611;</span>
      
      <span class="iconfont more-icon">&#xe66b;</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailInfo'
}
</script>

<style lang="stylus" scoped>
  @import "~styles/varibles.styl"
  .info-container >>> .more-icon
    position :absolute
    right :.2rem
    top :50%
    transform :translateY(-50%)
    font-size :.28rem
  .info-container
    overflow :hidden
    margin-bottom :.2rem
    background :#fff
    .info-attr
      display :block
      .info-grade, .info-introduction
        width :50%
        float :left
        position :relative
        padding :.2rem
        box-sizing :border-box
        .grade-wraper
          display :block
          .fraction
            line-height :.78rem
            font-size :.48rem
            color :$orangeColor
          .grade
            color :$orangeColor
        .comment
          color :#999
          font-size :.20rem
        .attractions
          line-height :.78rem
          font-size :.30rem
        .time
          color :#999
          font-size :.20rem
    .address
      display :block
      line-height :.88rem
      position :relative
      clear :both
      padding-left :.2rem
      .site-icon
        color :#666
</style>



