<template>
  <div class="container" @click="closeGallary">
    <div class="wraper">
      <swiper :options="swiperOptions">
        <!-- slides -->
        <swiper-slide v-for="(item,index) of imgs" :key="index">
          <img class="gallary-img" :src="item">
        </swiper-slide>
        <div class="swiper-pagination"  slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Gallary',
  props: {
    imgs: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      swiperOptions: {
        pagination: '.swiper-pagination',
        paginationType: 'fraction',
        observeParents: true,
        observer: true
      }
    }
  },
  methods: {
    closeGallary () {
      this.$emit('closeGallary')
    }
  }
}
</script>

<style lang="stylus" scoped>
  .container >>> .swiper-container
    overflow :inherit
  .container
    display :flex
    flex-direction :column
    justify-content :center
    overflow :hidden
    z-index :99
    position :fixed
    top :0
    bottom :0
    left :0
    right :0
    background :#000
    .wraper
      width :100%
      .gallary-img
        width 100%
      .swiper-pagination
        color :#fff
        bottom :-1rem
</style>



