<template>
  <div>
    <div class="search">
      <input v-model="keyword" class="search-input" placeholder="">
    </div>
    <div class="search-list" ref="wraper" v-show="keyword">
      <ul>
        <li
          class="search-item border-bottom"
          v-for="item of list"
          :key="item.id"
          @click="handleCityClick(item.name)"
        >
          {{item.name}}
        </li>
        <li class="search-item" v-show="NoDataList"></li>
      </ul>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import { mapMutations } from 'vuex'
export default {
  name: 'CitySearch',
  data () {
    return {
      keyword: '',
      list: [],
      timer: null
    }
  },
  props: {
    citys: Object
  },
  methods: {
    handleCityClick (city) {
      this.changecity(city)
      this.$router.push('/')
      this.keyword = ''
    },
    ...mapMutations(['changecity'])
  },
  computed: {
    NoDataList () {
      return !this.list.length
    }
  },
  mounted () {
    this.scroll = new BScroll(this.$refs.wraper)
  },
  watch: {
    keyword () {
      if (!this.keyword) {
        this.list = []
        return
      }
      if (this.timer) {
        clearTimeout(this.timer)
      }
      this.timer = setTimeout(() => {
        const results = []
        for (let i in this.citys) {
          this.citys[i].forEach((value) => {
            if (value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1) {
              results.push(value)
            }
          })
        }
        this.list = results
      }, 100)
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .search
    height :.72rem
    padding :0 .1rem
    background : $bgColor
    .search-input
     box-sizing :border-box
     width :100%
     height :.62rem
     line-height :.62rem
     text-align :center
     color :#666
     border-radius :.1rem
     padding :0 .1rem
  .search-list
    position :absolute
    top :1.58rem
    bottom :0
    left :0
    right :0
    z-index :1
    background :#eee
    .search-item
     line-height :.62rem
     padding-left :.2rem
     background :#fff
     color :#666
</style>



