<template>
  <div class="list" ref="wrapper">
    <div>
      <div class="area">
        <div class="title border-topbottom"></div>
        <div class="button-list">
          <div class="button-wraper">
            <div class="btn">{{this.currentCity}}</div>
          </div>
        </div>
      </div>
      <div class="area">
        <div class="title border-topbottom"></div>
        <div class="button-list">
          <div
            class="button-wraper"
            v-for="item of hotcities"
            :key="item.id"
            @click="handleCityClick(item.name)"
          >
            <div class="btn">{{item.name}}</div>
          </div>
        </div>
      </div>
      <div
        class="area"
        v-for="(item,key) of citys"
        :key="key"
        :ref="key"
      >
        <div class="title border-topbottom">{{key}}</div>
        <div class="item-list">
          <div class="item border-bottom"
               v-for="iteminter of item"
               :key="iteminter.id"
               @click="handleCityClick(iteminter.name)"
          >
            {{iteminter.name}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'CityList',
  props: {
    hotcities: Array,
    citys: Object,
    letter: String
  },
  methods: {
    handleCityClick (city) {
      this.changecity(city)
      this.$router.push('/')
    },
    ...mapMutations(['changecity'])
  },
  computed: {
    ...mapState({
      currentCity: 'city'
    })
  },
  watch: {
    letter () {
      if (this.letter) {
        const element = this.$refs[this.letter][0]
        this.scroll.scrollToElement(element)
      }
    }
  },
  mounted () {
    this.scroll = new BScroll(this.$refs.wrapper)
  }
}
</script>

<style lang="stylus" scoped>
  .border-topbottom
    &:before
     border-color: #ccc
    &:after
     border-color: #ccc
  .border-bottom
  &:before
    border-color: #ccc
  .list
    overflow :hidden
    position :absolute
    top: 1.58rem
    bottom :0
    left :0
    right :0
    .title
      background :#f2f2f2
      line-height :.8rem
      font-size :.28rem
      padding-left :.2rem
      color: #666
    .button-list
      overflow :hidden
      padding :.1rem .6rem .1rem .1rem
      .button-wraper
        float :left
        width :33.33%
        .btn
         margin :.1rem
         text-align :center
         border: .02rem solid #ccc
         line-height: .48rem
         border-radius :.1rem
         color: #666
    .item-list

      .item
        line-height :.76rem
        padding-left: .2rem
</style>



