<template>
  <div>
    <div
      @click="back"
      class="header-Abs"
      v-show="showAbs"
    >
      <div class="iconfont header-Abs-back">&#xe624;</div>
    </div>
    <div
      class="header-fixed"
      v-show="!showAbs"
      :style="opacityStyle"
    >
      
      <router-link to="/">
        <div class="iconfont header-fixed-back">&#xe624;</div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailHeader',
  data () {
    return {
      showAbs: true,
      opacityStyle: {
        opacity: 0
      }
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    handleScroll () {
      const top = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop
      if (top > 60) {
        let opacity = top / 140
        opacity = opacity > 1 ? 1 : opacity
        this.opacityStyle = { opacity }
        this.showAbs = false
      } else {
        this.showAbs = true
      }
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll)
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .header-Abs
    position :absolute
    top :.2rem
    left :.2rem
    width :.8rem
    height :.8rem
    border-radius :.4rem
    background :rgba(0,0,0,.8)
    line-height :.8rem
    text-align :center
    .header-Abs-back
      color :#fff
  .header-fixed
    z-index :2
    position :fixed
    top :0
    left :0
    right :0
    height: $headerHeight
    line-height: $headerHeight
    background: $bgColor
    text-align center
    color: #fff
    font-size :.32rem
    .header-fixed-back
     position :absolute
     top: 0
     left :0
     width :.64rem
     text-align :center
     color :#fff
     font-size : .4rem
</style>



