<template>
  <div>
    <city-header></city-header>
    <city-search :citys="citys"></city-search>
    <city-list
      :hotcities="hotcities"
      :citys="citys"
      :letter="letter"
    ></city-list>
    <city-alphabet
      :citys="citys"
      @change="handleLetterChange"
    ></city-alphabet>
  </div>
</template>

<script>
import axios from 'axios'
import CityHeader from './components/CityHeader'
import CitySearch from './components/CitySearch'
import CityList from './components/CityList'
import CityAlphabet from './components/Alphabet'

export default {
  name: 'City',
  data () {
    return {
      hotcities: [],
      citys: {},
      letter: ''
    }
  },
  components: {
    CityHeader,
    CitySearch,
    CityList,
    CityAlphabet
  },
  methods: {
    getCityInfo () {
      axios.get('https://www.easy-mock.com/mock/5b4d76eedb60f15d335e0592/example/api/city.json')
        .then(this.handleGetCityInfoSucc)
    },
    handleGetCityInfoSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.hotcities = data.hotCities
        this.citys = data.cities
      }
    },
    handleLetterChange (letter) {
      this.letter = letter
    }
  },
  mounted () {
    this.getCityInfo()
  }

}
</script>

<style scoped>

</style>



