<template>
  <div class="header">
    
    <router-link to="/">
      <div class="iconfont back-header">&#xe624;</div>
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'CityHeader'
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .header
    position :relative
    overflow :hidden
    height: $headerHeight
    line-height: $headerHeight
    background: $bgColor
    text-align center
    color: #fff
    font-size :.32rem
    .back-header
      position :absolute
      top: 0
      left :0
      width :.64rem
      text-align :center
      color :#fff
      font-size : .4rem
</style>



