<template>
  <div class="header">
    <div class="header-left">
      <div class="iconfont back-icon">&#xe624;</div>
    </div>
    <div class="header-input">
      <span class="iconfont search-icon">&#xe632;</span>
      //
    </div>
    <router-link to="/city">
      <div class="header-right">
        {{this.city}}
        <span class="iconfont arrow-icon">&#xe64a;</span>
      </div>
    </router-link>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'HomeHeader',
  computed: {
    ...mapState(['city'])
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .header
    line-height: $headerHeight
    display :flex
    background :$bgColor
    color :#fff
   .header-left
      width : .64rem
      float left
      margin-left :.1rem
      .back-icon
       text-align: center
       font-size : .4rem
   .header-input
      flex:1
      background :#fff
      border-radius :.1rem
      margin:.12rem 0 0 .2rem
      height :.62rem
      line-height :.62rem
      color:#ccc
      .search-icon
        margin-left : .2rem
   .header-right
      min-width :1.04rem
      padding :0 .1rem
      float :right
      text-align center
      color: #fff
      .arrow-icon
        margin-left :-.04rem
        font-size : .24rem
</style>



