<template>
  <div class="recommeng-wrap">
    <div class="item-title">
      <span class="iconfont item-title-icon">&#xe600;</span>
    </div>
    <ul class="recommeng-info-wrap">
      <li class="info-wraper border-top" v-for="item of recommendlist" :key="item.id">
        <div class="recommend-info">
          <div class="recommend-info-title">{{item.title}}</div>
          <ul>
            <span class="recommend-info-mp-ticket" v-show="item.tomorrow">
              <img class="mp-ticket-tagicon" src="https://img1.qunarzz.com/piao/fusion/1804/25/792e9929973a9902.png">
              
            </span>
          </ul>
          <ul>
            <span class="mp-ticket-label" v-for="(attr,index) of item.attrs" :key="index">
              <img class="mp-ticket-label-tagicon" :src="attr.img" v-show="attr.imgShow">
              {{attr.title}}
            </span>
          </ul>
        </div>
        <div class="recommend-price">
          <div class="recommend-price-wraper">
            <div class="price-text">{{item.price}}</div>
            <button class="price-btn"></button>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'DetailRecommend',
  props: {
    recommendlist: Array
  }
}
</script>

<style lang="stylus" scoped>
  .border-top
    &:before
      border-color :#ccc
  .recommeng-wrap
    padding-left :.2rem
    .item-title
      line-height :1rem
      font-size :.32rem
      .item-title-icon
        color :rgb(255,110,110)
        padding-right :.1rem
    .recommeng-info-wrap
      padding-right :.2rem
      .info-wraper
        display :flex
        width :100%
        overflow :hidden
        .recommend-info
          float :left
          flex :1
          padding :.2rem 0
          .recommend-info-title
            font-size :.32rem
            line-height :.48rem
          .recommend-info-mp-ticket
            font-size :.24rem
            line-height :.48rem
            .mp-ticket-tagicon
              display: inline-block
              width: .25rem
              max-height: .32rem
              margin-right: .04rem
          .mp-ticket-label
            border :1px solid #a5e4ec
            font-size :.24rem
            line-height :.48rem
            color :#00afc7
            border-radius :.08rem
            margin-right :.1rem
            .mp-ticket-label-tagicon
              display: inline-block
              width: .25rem
              max-height: .32rem
              margin-right: .04rem
        .recommend-price
          float :left
          width :30%
          text-align :center
          position :relative
          .recommend-price-wraper
            position :absolute
            top :50%
            transform :translateY(-50%)
            right :0
            .price-text
              font-size :.38rem
              color: #ff9800
            .price-btn
              padding :0 .6rem
              line-height :.58rem
              border-radius :.08rem
              color :#fff
              background-image: linear-gradient(130deg,#ffab1e 37%,#ff8c12 100%);
</style>



