<template>
  <div class="other-container">
    <div class="other-title"></div>
    <ul>
      <li class="other-wraper-li" v-for="item of otherList" :key="item.id">
        <div class="img-wraper">
          <img class="img-item" :src="item.img" alt="">
        </div>
        <div class="content-wraper">
          <div class="attr">
            <span class="attr-title">{{item.title}}</span>
            <span class="attr-price">{{item.price}}<i class="gary-color"></i></span>
          </div>
          <div class="evaluation">
            <span class="iconfont evaluation-grade" :style="{ width: getScore(item.score) + '%'}">&#xe60b;&#xe60b;&#xe60b;&#xe60b;&#xe60b;</span>
            <span class="iconfont evaluation-grade-default">&#xe60b;&#xe60b;&#xe60b;&#xe60b;&#xe60b;</span>
          </div>
          <div class="area">
            <span>{{item.area}}</span>
            <span class="evaluation-comment">{{item.commentNum}}</span>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'DetailOther',
  data () {
    return {
      otherList: [{
        id: '001',
        title: '',
        price: '37.4',
        commentNum: '401',
        score: '4',
        area: '',
        img: 'http://img1.qunarzz.com/sight/p0/1511/bd/bd356ef0dc69cbf590.img.jpg_228x168_1b8c0c77.jpg'
      }, {
        id: '002',
        title: '',
        price: '37.4',
        commentNum: '401',
        score: '2.5',
        area: '',
        img: 'http://img1.qunarzz.com/sight/p0/1511/bd/bd356ef0dc69cbf590.img.jpg_228x168_1b8c0c77.jpg'
      }]
    }
  },
  methods: {
    getScore (val) {
      return val / 5 * 100
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import "~styles/varibles.styl"
  .other-container
    background :#fff
    margin-top :.2rem
    .other-title
      position :relative
      color: #333
      font-size: .3rem
      line-height: .88rem
      height :.88rem
      text-indent: .2rem
      margin-left :.2rem
    .other-title:after
      content :''
      position :absolute
      top :.31rem
      left :0
      width: .06rem
      height: .26rem
      background: $bgColor
    .other-wraper-li
      display :flex
      overflow :hidden
      padding :.2rem
      .img-wraper
        display :block
        .img-item
          width :1.62rem
          height :1.62rem
      .content-wraper
        position :relative
        flex :1
        padding-left :.2rem
        .attr, .evaluation
          display :inline-block
          width :100%
          .attr-title
            float :left
            line-height :.54rem
            font-size :.32rem
          .attr-price
            float :right
            line-height :.54rem
            font-size :.38rem
            color :$orangeColor
            .gary-color
              color :#999
              font-size :.24rem
        .evaluation
          position :relative
          display :inline-block
          width :1.4rem
          height :.32rem
          .evaluation-grade
            position :absolute
            top :0
            left :0
            font-size :.22rem
            color :#ffb436
            z-index :2
            overflow :hidden
          .evaluation-grade-default
            position :absolute
            top :0
            left :0
            font-size :.22rem
            margin-right :.5rem
            color :#ccc
        .area
          position :absolute
          bottom :0
          color :#999
          font-size :.28rem
          width :100%
          .evaluation-comment
            color :#999
            font-size :.28rem
            float :right
            margin-right :.2rem
</style>



