<style lang="stylus" scoped>
  @import "~styles/varibles.styl"
  .header
    display :flex
    height :$headerHeight
    background :$bgColor
    .header-left
      width : .64rem
      float left
      margin-left :.1rem
      .back-icon
        color :#fff
        text-align: center
        font-size : .4rem
        line-height :$headerHeight
    .header-title
      text-align :center
      flex :1
      line-height :$headerHeight
      color :#fff
      font-size : .32rem
</style>



