<template>
  <div class="list-container">
    <div class="list-title">
      <img class="title-icon" src="http://img1.qunarzz.com/piao/fusion/1711/89/ebc329f16c55bb02.png" alt="icon">
      
    </div>
    <ul>
      <router-link
        tag="li"
        class="list-item border-bottom"
        v-for="item of list"
        :key="item.id"
        :to="'/detail/'+ item.id"
      >
        <img class="item-img" :src="item.imgUrl" alt="">
        <div class="item-content">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
          <div class="item-wraper">
            <button class="item-btn"></button>
            <span class="item-dict">{{item.disc}}</span>
          </div>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeList',
  props: {
    list: Array
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/mixins.styl'
  .list-container
    background :#fff
    margin-top :.2rem
    .list-title
     line-height :.90rem
     font-size :.32rem
     color :#212121
     text-indent :.2rem   //
     .title-icon
      display :inline-block
      width :.3rem
      height :.3rem
      line-height :.90rem
    .list-item
     overflow :hidden
     display :flex
     height :2.4rem
     .item-img
      width : 2rem
      height : 2rem
      padding : .2rem
     .item-content
       flex :1
       min-width :0
       padding :.2rem 0
       .item-title
        line-height :.8rem
        font-size :.32rem
        ellipsis()
       .item-desc
        color :#ccc
        ellipsis()
       .item-wraper
         padding :.28rem .2rem .28rem 0
         .item-btn
          background :#ffb436
          color :white
          padding :0 .2rem
          line-height :.4rem
          font-size :.2rem
          border-radius :.1rem
         .item-dict
           float :right
           line-height :.4rem
</style>



