<template>
  <div class="header-container border-bottom">
    <div class="header-left">
      <router-link tag="div" to="/" class="iconfont back-icon">&#xe624;</router-link>
    </div>
    <div class="header-input-wraper">
      <input class="header-input" type="" placeholder="">
    </div>
    <div class="header-right">
      
    </div>
  </div>
</template>

<script>
export default {
  name: 'IconHeader'
}
</script>

<style lang="stylus" scoped>
  @import "~styles/varibles.styl"
  .header-container
    display :flex
    width :100%
    height :$headerHeight
    background :#fff
    .header-left
      float :left
      width : $headerHeight
      .back-icon
        text-align :center
        line-height: $headerHeight
    .header-input-wraper
      flex :1
      background :#fff
      line-height: $headerHeight
      .header-input
        background :#eee
        width :100%
        border-radius :.2rem
        line-height: .58rem
        text-align :center
    .header-right
      float :right
      width :$headerHeight
      text-align :center
      line-height: $headerHeight
      padding-right :.1rem
</style>



