<template>
  <div class="nav-container border-bottom" :class="{ fixed:isfixed }">
    <div class="nav-item border-right">
      
      <span class="iconfont more-icon">&#xe60c;</span>
    </div>
    <div class="nav-item">
      
      <span class="iconfont more-icon">&#xe60c;</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'IconNav',
  data () {
    return {
      isfixed: false
    }
  },
  methods: {
    handleScroll () {
      const top = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop
      if (top > 43) {
        this.isfixed = true
      } else {
        this.isfixed = false
      }
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll)
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style lang="stylus" scoped>
  @import "~styles/varibles.styl"
  .fixed
    position :fixed
    top :0
    z-index :5
  .nav-container
    width :100%
    height :$headerHeight
    background :#fff
    .nav-item
      float :left
      width :50%
      text-align :center
      line-height :$headerHeight
      font-size :.28rem
      .more-icon
        font-size :.22rem
</style>



