<template>
  <div class="wraper">
    <swiper :options="swiperOption">
      <swiper-slide v-for="(list,index) of pages" :key="index">
        <router-link tag="div" to="/icon/" class="icons" v-for="item of list" :key="item.id">
          <div class="icons-img">
            <img class="icons-img-content" :src="item.imgUrl" alt="">
          </div>
          <p class="icon-desc">{{item.desc}}</p>
        </router-link>
      </swiper-slide>
      <p class="swiper-pagination"  slot="pagination"></p>
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
        autoplay: false,
        pagination: '.swiper-pagination'
      }
    }
  },
  computed: {
    pages () {
      const pages = []
      this.list.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  @import '~styles/mixins.styl'
  .wraper >>> .swiper-pagination-bullets
    bottom :-.1rem
  .wraper >>> .swiper-container
    overflow :initial
  .wraper
    height :0
    padding-bottom :53%
    overflow :hidden
    margin-top :.06rem
    background :#fff
    .icons
     position :relative
     float :left
     width :25%
     height :0
     padding-bottom :25%
     .icons-img
      position :absolute
      top: 0
      left :0
      right :0
      bottom :.44rem
      box-sizing :border-box
      padding :.14rem
      .icons-img-content
        display block
        margin :0 auto
        height :100%
     .icon-desc
      position :absolute
      bottom :.24rem
      left :0
      right: 0
      color :$darkColor
      text-align :center
      ellipsis ()

</style>



