<template>
  <div class="tab-container">
    <div class="tab-title border-bottom" id="tab-title" ref="tabTitle" :class="{ fixed:isfixed}">
      <div class="tab-title-ticket" @click="handleTicketArea" v-bind:class="{ active:isActive }"></div>
      <div class="tab-title-ticket" @click="handleDayArea" v-bind:class="{ active:!isActive }"></div>
    </div>
    <div class="tab-content">
      <ul ref="ticketArea">
        <li class="tab-info-container-li" v-for="item of ticketList" :key="item.id">
          <div class="tab-item-title">
            <span class="tab-item-title-icon"></span>
            {{item.scenicSpot}}
          </div>
          <ul class="tab-info-container-ul" v-if="item.tickets">
            <li class="tab-info-li border-top" v-for="(ticket,index) of item.tickets" :key="index">
              <div class="tab-info-title-wraper">
                {{ticket.title}}
                <span class="tab-info-small-title" v-if="ticket.isLimit">{{ticket.limit}}</span>
              </div>
              <div class="tab-price">
                <div class="tab-price-wraper">
                  {{ticket.price}}
                  <span class="gray-color"></span>
                  <span class="iconfont more-icon">&#xe60c;</span>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
      <ul ref="dayArea">
        <li class="tab-info-container-li">
          <div class="tab-item-title">
            <span class="tab-item-title-icon"></span>
            
          </div>
          <ul class="tab-info-container-ul">
            <li class="tab-info-li border-top" v-for="(item,index) of showdetailList" :key="index">
              <div class="tab-info-title-wraper">
                {{item.title}}
              </div>
              <div class="tab-price">
                <div class="tab-price-wraper">
                  {{item.price}}
                  <span class="gray-color"></span>
                  <span class="iconfont more-icon">&#xe60c;</span>
                </div>
              </div>
            </li>
            <div class="toggle" v-if="dayTicketList.length > 2" v-on:click="changeFoldState" v-show="brandFold">
              <span class="iconfont"> &nbsp;&#xe60c;</span>
            </div>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailTab',
  data () {
    return {
      isfixed: false,
      isActive: true,
      tabTitleTop: '',
      brandFold: true,
      dayTicketList: [{
        title: '+1',
        price: '198'
      }, {
        title: '+2',
        price: '428'
      }, {
        title: '+7',
        price: '400'
      }],
      ticketList: [{
        id: '0001',
        scenicSpot: '',
        tickets: [{
          title: '',
          price: '117.8',
          isLimit: false,
          limit: ''
        }, {
          title: '',
          price: '57.8',
          isLimit: true,
          limit: ''
        }]
      }, {
        id: '0002',
        scenicSpot: '',
        tickets: [{
          title: '',
          price: '76.8',
          isLimit: false,
          limit: ''
        }, {
          title: '',
          price: '37.8',
          isLimit: true,
          limit: ''
        }]
      }, {
        id: '0003',
        scenicSpot: '',
        tickets: [{
          title: '+',
          price: '168',
          isLimit: false,
          limit: ''
        }, {
          title: '+',
          price: '95',
          isLimit: true,
          limit: ''
        }, {
          title: '',
          price: '999',
          isLimit: false,
          limit: ''
        }]
      }]
    }
  },
  computed: {
    getheight () {
      return this.$refs.tabTitle.offsetTop
    },
    showdetailList: {
      get: function () {
        if (this.brandFold) {
          if (this.dayTicketList.length < 3) {
            return this.dayTicketList
          }
          let newArr = []
          for (var i = 0; i < 2; i++) {
            let item = this.dayTicketList[i]
            newArr.push(item)
          }
          return newArr
        }
        return this.dayTicketList
      },
      set: function (val) {
        this.showdetailList = val
      }
    }
  },
  methods: {
    handleDayArea () {
      const height = this.$refs.dayArea.offsetTop - 100
      window.scrollTo({top: height})
      this.isActive = false
    },
    handleTicketArea () {
      const height = this.$refs.ticketArea.offsetTop - 100
      window.scrollTo({top: height})
      this.isActive = true
    },
    handleScroll () {
      const top = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop
      const height = this.getheight
      if (top > height) {
        this.isfixed = true
      } else {
        this.isfixed = false
      }
    },
    changeFoldState () {
      this.brandFold = !this.brandFold
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll)
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .border-top, .border-bottom
    &:before
      border-color :#ccc
  .tab-container
    margin-top :.2rem
    background :#fff
    .fixed
      position :fixed
      top :$headerHeight
      left :0
      right :0
    .tab-title
      width :100%
      display :inline-flex
      overflow :hidden
      background :#fff
      z-index :1
      .tab-title-ticket
        float :left
        width :50%
        text-align :center
        line-height :1rem
        font-size :.32rem
      .active
        border-bottom :.03rem solid $bgColor
    .tab-content
      overflow :hidden
      background :#f5f5f5
      .tab-item-title
        line-height :1rem
        padding :0 .2rem
        .tab-item-title-icon
          display: inline-block;
          position: relative;
          width: .36rem;
          height: .36rem;
          top: .1rem;
          left: .1rem;
          background: url(http://s.qunarzz.com/piao/image/touch/sight/detail.png) 0 -.45rem no-repeat;
          margin-right: .1rem;
          background-size: .4rem 3rem;
      .tab-info-container-li
        overflow :hidden
        margin-bottom :.2rem
        background :#fff
        .toggle
          line-height :.88rem
          text-align :center
          color: #333
        .tab-info-li
          display :flex
          padding :.4rem .2rem
          .tab-info-title-wraper
            flex :1
            font-size :.28rem
            .tab-info-small-title
              font-size: .24rem;
              color: #888;
              line-height: .32rem;
          .tab-price
            width :35%
            position :relative
            .tab-price-wraper
              font-size :.38rem
              color: #ff9800
              position :absolute
              right :0
              top: 50%
              transform :translateY(-50%)
              .gray-color
                color :#999
                font-size :.22rem
              .more-icon
                color :#999
                font-size :.28rem
</style>



