<template>
  <div class="nav-container border-top">
    <div class="nav-site border-right">
      <div class="nav-wrap-text">
        <span class="iconfont nav-icon">&#xe611;</span>
      </div>
    </div>
    <div class="nav-play">
      <div class="nav-wrap-text">
        <span class="iconfont nav-icon">&#xe60a;</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeNav'
}
</script>

<style lang="stylus" scoped>
  .border-top, .border-right
    &:before
      border-color: #ccc
  .nav-container >>> .nav-wrap-text
    position :absolute
    top :50%
    transform :translateY(-50%)
    -webkit-transform: translateY(-50%)
    bottom :0
    left :0
    right :0
    text-align :center
    line-height :.54rem
  .nav-container >>> .nav-icon
    padding-right :.1rem
  .nav-container
    width :100%
    background :#fff
    height :0
    padding-bottom :12.5%
    .nav-site, .nav-play
      position :relative
      float :left
      width :50%
      height :0
      padding-bottom :12.5%
</style>



