<template>
  <div>
    <detail-banner
      :sightName="sightName"
      :bannerImg="bannerImg"
      :gallaryImgs="gallaryImgs"
    ></detail-banner>
    <detail-info></detail-info>
    <detail-header class="header"></detail-header>
    <div class="content">
      <detail-recommend :recommendlist="recommendlist" class="recommend"></detail-recommend>
      <detail-tab></detail-tab>
      <detail-comment :commentList="commentList"></detail-comment>
      <detail-other></detail-other>
    </div>
  </div>
</template>

<script>
import DetailBanner from './components/DetailBanner'
import DetailHeader from './components/DetailHeader'
import DetailRecommend from './components/DetailRecommend'
import DetailTab from './components/DetailTab'
import DetailComment from './components/DetailComment'
import DetailOther from './components/DetailOther'
import DetailInfo from './components/DetailInfo'
import axios from 'axios'
export default {
  name: 'Detail',
  components: {
    DetailBanner,
    DetailHeader,
    DetailRecommend,
    DetailTab,
    DetailComment,
    DetailOther,
    DetailInfo
  },
  data () {
    return {
      sightName: '',
      bannerImg: '',
      gallaryImgs: [],
      recommendlist: [],
      commentList: []
    }
  },
  methods: {
    getDetailInfo () {
      axios.get('https://www.easy-mock.com/mock/5b4d76eedb60f15d335e0592/example/api/detail.json', {
        params: {
          id: this.$route.params.id
        }
      }).then(this.getDetailInfoSucc)
    },
    getDetailInfoSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.sightName = data.sightName
        this.bannerImg = data.bannerImg
        this.gallaryImgs = data.gallaryImgs
        this.recommendlist = data.recommendlist
        this.commentList = data.commentList
      }
    }
  },
  mounted () {
    this.getDetailInfo()
  }
}
</script>

<style lang="stylus" scoped>
  .content
    background :#f5f5f5
    .recommend
      background :#fff
</style>



